<template>
  <div id="app">
    <div id="nav">
      <img src="./assets/back.png" alt class="img1" @click="backLogin" />
      <img src="./assets/search.png" alt class="img2" />
      <p>扫码交货</p>
      <div @click="showtj = true">订单统计</div>
    </div>
    <div class="titile">
      <div class="box">
        <div class="top">编号</div>
        <div class="bottom">{{title.dddh }}</div>
      </div>
      <div class="box">
        <div class="top">客户</div>
        <div class="bottom">{{title.khmc}}</div>
      </div>
    </div>
    <div class="content">
      <div class="top">
        <div class="box">品名</div>
        <div class="box">工序</div>
        <div class="box">颜色</div>
        <div class="box">规格</div>
        <div class="box">匹数</div>
        <div class="box">数量</div>
      </div>
      <ul @scroll="scrollEvent">
        <li v-for="(item,index) in msg" :key="index">
          <div class="bbox" @click="showJl(item,index)">
            <div class="box">{{item.pm}}</div>
            <div class="box">{{item.gxmc}}</div>
            <div class="box">{{item.ysmc}}</div>
            <div class="box">{{item.gg}}</div>
          </div>
          <div class="box" @click="xgps(parseFloat(item.ps),item,index)">{{item.ps}}</div>
          <div class="box" @click="jh(parseFloat(item.sl),item,index)">{{parseFloat(item.sl)}}</div>
        </li>
      </ul>
    </div>
    <div class="btn" @click="sys">扫一扫</div>
    <transition>
      <jhjl @tap="jlTap" v-if="showjl"></jhjl>
    </transition>
    <transition>
      <ddtj @tap="ddTap" v-if="showtj"></ddtj>
    </transition>

    <transition name="v2">
      <div class="mask" v-if="maskShow">
        <div class="contentbox" :key="'a'">
          <div class="contentB">
            <div class="title">是否确认交货？</div>
            <div class="msg">
              <p>交货数量：</p>
              <input type="number" v-model="jhsl" />
            </div>
            <div class="btnbox">
              <div class="btn1" @click="cancel">取消</div>
              <div class="btn2" @click="confirm">确定</div>
            </div>
          </div>
        </div>
      </div>
    </transition>
    <transition name="v2">
      <div class="mask" v-if="maskShow2">
        <div class="contentbox" :key="'a2'">
          <div class="contentB">
            <div class="title">修改交货匹数</div>
            <div class="msg">
              <p>交货匹数：</p>
              <input type="number" v-model="jhps" />
            </div>
            <div class="btnbox">
              <div class="btn1" @click="cancel2">取消</div>
              <div class="btn2" @click="confirm2">确定</div>
            </div>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>
<script>
import jhjl from "./components/jl.vue";
import ddtj from "./components/ddtj.vue";
export default {
  data() {
    return {
      beforeS: "",
      showjl: false,
      showtj: false,
      d: false,
      maskShow: false,
      maskShow2: false,
      jhsl: 0,
      jhps: 0,
      selectItem: {},
      selectIndex: "",
      title: { dddh: "", khmc: "" },
      msg: []
    };
  },
  created() {
    window.scan = this.submit;
  },
  methods: {
    submit(dh) {
      this.$request("Jh/jhcx", { dh: dh }).then(res => {
        if (res.status == -1) {
          return this.$toast({
            message: "该员工没有负责第一道工序",
            duration: 1000
          });
        } else if (res.status == -2) {
          return this.$toast({
            message: "上一道工序未交货！",
            duration: 1000
          });
        }
        if (res.status == 0) {
          return this.$toast({
            message: "单号错误！",
            duration: 1000
          });
        }
        if (res && res.length !== 0 && res instanceof Array) {
          this.msg = res.reduce((total, item) => {
            return total.concat(item)
          }, []);
          this.title = { dddh: this.msg[0].dddh, khmc: this.msg[0].khmc };
        } else {
          this.$toast({
            message: "暂无数据！",
            duration: 1000
          });
        }
      });
    },
    backLogin() {
      this.$request('index/zxdl').then((res) => {
        if (res == 1) {
          window.location.href = this.$modulE;
        }
      })
    },
    cancel2() {
      this.maskShow2 = !this.maskShow2;
    },
    confirm2() {
      this.selectItem.ps = this.jhps;
      this.maskShow2 = !this.maskShow2;
    },
    //JSON.parse(localStorage.getItem('account'))
    cancel() {
      this.maskShow = !this.maskShow;
    },
    confirm() {
      this.selectItem.jhsl = this.selectItem.sl = this.jhsl;
      this.jhsl;
      this.$request("Jh/qdjh", Object.assign({}, this.selectItem)).then(res => {
        if (res == 1) {
          this.maskShow = !this.maskShow;
          // this.msg.splice(this.selectIndex, 1);
          this.$toast({
            message: "交货成功！",
            duration: 1000
          });
        } else {
          this.$toast({
            message: "交货失败！",
            duration: 1000
          });
        }
      });
    },
    xgps(num, item, i) {
      this.selectItem = item;
      this.jhps = num;
      this.selectIndex = i;
      this.maskShow2 = !this.maskShow2;
    },
    jh(num, item, i) {
      this.selectItem = item;
      this.jhsl = num;
      this.selectIndex = i;
      this.maskShow = !this.maskShow;
    },
    showJl() {
      this.showjl = !this.showjl;
    },
    jlTap() {
      this.showjl = !this.showjl;
    },
    ddTap() {
      this.showtj = !this.showtj;
    },
    sys() {
      /*eslint-disable-next-line*/
      webview = plus.webview
        .create("./scan.html", "scan", {
          backButtonAutoControl: "close",
          titleNView: {
            type: "float",
            backgroundColor: "rgba(215,75,40,0)",
            titleText: "扫一扫",
            titleColor: "#FFFFFF",
            autoBackButton: true,
            buttons: [
              {
                text: "相册",
                fontSize: "16px",
                onclick: "javascript:pick()"
              }
            ]
          }
        })
        .show("slide-in-right");
    },
    scrollEvent() {
      var afterS = event.target.scrollTop;
      var a = afterS - this.beforeS;
      if (a > 0) {
        if (
          event.target.scrollTop + event.target.offsetHeight >=
          event.target.scrollHeight
        ) {
          this.$toast({
            message: "已经到达底部",
            duration: 1000
          });
        }
      } else if (a < 0) {
        if (
          event.target.scrollTop + event.target.offsetHeight <=
          event.target.offsetHeight
        ) {
          this.$toast({
            message: "已经到达顶部",
            duration: 1000
          });
        }
      }
      this.beforeS = afterS;
    }
  },
  components: {
    jhjl,
    ddtj
  }
};
</script>
