import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import { Toast } from 'vant';
import axios from 'axios'
import qs from 'qs'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import {
    Loading
} from 'element-ui'
Vue.use(ElementUI)
Vue.use(Toast);
Vue.prototype.$toast = Toast;
Vue.config.productionTip = false;
Vue.prototype.$request = function (url, data, type) {
  localStorage.setItem('account', JSON.stringify({
      zh: 123
  }))
  type = type || 'post';
  var obj = {
      zh: JSON.parse(localStorage.getItem('account')).zh || ''
  };
  Object.assign(obj, data);
  obj = qs.stringify(obj);
  let loadingInstance;
  axios.interceptors.request.use(config => {
      // fnReq()
      loadingInstance = Loading.service({
          fullscreen: true
      });
      return config;
  }, err => {
      return Promise.reject(err);
  });
  axios.interceptors.response.use((response) => {
      // fnRes(response);
      loadingInstance.close();
      return response;
  }, (error) => {
      return Promise.reject(error);
  });
  var p = new Promise(function (resolve, reject) {
      axios[type](Vue.prototype.$url + url, obj,{withCredentials:true}).then(res => {
          resolve(res.data);
      }).catch(error => {
          reject(error);
      });

  });
  return p;
};
Vue.prototype.$axios = axios;
Vue.prototype.$qs = qs;
// eslint-disable-next-line
Vue.prototype.$url = "http://192.168.8.240/LYSoft_RC/index.php/home/";
//eslint-disable-next-line
Vue.prototype.$modulE = modulE;
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
