<template>
  <div class="app">
    <div id="nav">
      <img src="../assets/back.png" alt class="img1" @click="$emit('tap')" />
      <img src="../assets/search.png" alt class="img2" />
      <p>生产单明细</p>
    </div>
    <div class="titile">
      <div class="box">
        <div class="top">单号</div>
        <div class="bottom">{{title.dh }}</div>
      </div>
      <div class="box">
        <div class="top">客户</div>
        <div class="bottom">{{title.khmc}}</div>
      </div>
      <div class="box">
        <div class="top">客户</div>
        <div class="bottom">{{title.pm}}</div>
      </div>
    </div>
    <div class="content">
      <div class="top">
        <div class="box">工序</div>
        <div class="box">颜色</div>
        <div class="box">匹数</div>
        <div class="box">数量</div>
        <div class="box">状态</div>
      </div>
      <ul @scroll="scrollEvent">
        <li v-for="(item,index) in msg" :key="index">
          <div class="box">{{item.gxmc}}</div>
          <div class="box">{{item.ysmc}}</div>
          <div class="box">{{item.ps}}</div>
          <div class="box">{{parseFloat(item.jhsl)}}</div>
          <div class="box">{{item.jhzt}}</div>
        </li>
      </ul>
    </div>
    <!-- <div class="btn" @click="sys">扫一扫</div> -->
  </div>
</template>
<script>
export default {
  data() {
    return {
      beforeS: "",
      showjl: false,
      showtj: false,
      showxq: false,
      d: false,
      maskShow: false,
      maskShow2: false,
      jhsl: 0,
      jhps: 0,
      selectItem: {},
      selectIndex: "",
      title: { dddh: "", khmc: "" },
      msg: []
    };
  },
  props: {
    option: {}
  },
  created() {
    window.scan = this.submit;
    this.get();
  },
  methods: {
    get() {
      this.$request("scdjd/cxscdmx", this.option).then(res => {
        if (res && res.length !== 0 && res instanceof Object) {
          this.msg = res.jl;
          this.title = res.dj[0];
        } else {
          this.$toast({
            message: "暂无数据！",
            duration: 1000
          });
        }
      });
    },
    submit(dh) {
      this.$request("Jh/jhcx", { dh: dh }).then(res => {
        if (res.status == -1) {
          alert("该员工没有负责第一道工序");
          return;
        } else if (res.status == -2) {
          alert("上一道工序未交货！");
          return;
        }
        if (res.status == 0) {
          return alert("单号错误！");
        }
        if (res && res.length !== 0 && res instanceof Array) {
          this.msg = res.map(item => item[0]);
          this.title = { dddh: this.msg[0].dddh, khmc: this.msg[0].khmc };
          window.console.log(this.msg);
        } else {
          alert("暂无数据！");
        }
      });
    },
    cancel2() {
      this.maskShow2 = !this.maskShow2;
    },
    confirm2() {
      this.selectItem.ps = this.jhps;
      this.maskShow2 = !this.maskShow2;
    },
    //JSON.parse(localStorage.getItem('account'))
    cancel() {
      this.maskShow = !this.maskShow;
    },
    confirm() {
      this.selectItem.jhsl = this.selectItem.sl = this.jhsl;
      this.jhsl;
      this.maskShow = !this.maskShow;
      this.$request("Jh/qdjh", Object.assign({}, this.selectItem)).then(res => {
        if (res.status == 1) {
          this.maskShow = !this.maskShow;
          // this.msg.splice(this.selectIndex, 1);
          alert("交货成功！");
        } /* else if () {
          
        } */
      });
    },
    xgps(num, item, i) {
      this.selectItem = item;
      this.jhps = num;
      this.selectIndex = i;
      this.maskShow2 = !this.maskShow2;
    },
    jh(num, item, i) {
      this.selectItem = item;
      this.jhsl = num;
      this.selectIndex = i;
      this.maskShow = !this.maskShow;
    },
    showJl() {
      this.showjl = !this.showjl;
    },
    jlTap() {
      this.showjl = !this.showjl;
    },
    ddTap() {
      this.showtj = !this.showtj;
    },
    djTap() {
      this.showxq = !this.showxq;
    },
    sys() {
      /*eslint-disable-next-line*/
      webview = plus.webview
        .create("scan.html", "scan", {
          backButtonAutoControl: "close",
          titleNView: {
            type: "float",
            backgroundColor: "rgba(215,75,40,0)",
            titleText: "扫一扫",
            titleColor: "#FFFFFF",
            autoBackButton: true,
            buttons: [
              {
                text: "相册",
                fontSize: "16px",
                onclick: "javascript:pick()"
              }
            ]
          }
        })
        .show("slide-in-right");
    },
    scrollEvent() {
      var afterS = event.target.scrollTop;
      var a = afterS - this.beforeS;
      if (a > 0) {
        if (
          event.target.scrollTop + event.target.offsetHeight >=
          event.target.scrollHeight
        ) {
          this.$toast({
            message: "已经到达底部",
            duration: 1000
          });
        }
      } else if (a < 0) {
        if (
          event.target.scrollTop + event.target.offsetHeight <=
          event.target.offsetHeight
        ) {
          this.$toast({
            message: "已经到达顶部",
            duration: 1000
          });
        }
      }
      this.beforeS = afterS;
    }
  },
  components: {}
};
</script>
<style lang="scss">
.v2-enter .contentB,
.v2-leave-to .contentB {
  transform: scale(0.7);
}
.v2-enter-active .contentB,
.v2-leave-active .contentB {
  transition: all 0.5s ease;
}
.v2-enter,
.v2-leave-to {
  opacity: 0;
}
.v2-enter-active,
.v2-leave-active {
  transition: all 0.5s ease;
}
.mask {
  position: fixed;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  .contentbox {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    .contentB {
      width: 80%;
      height: 156px;
      background-color: white;
      border-radius: 8px;
      display: flex;
      flex-direction: column;
      align-items: center;
      .title {
        height: 40px;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        color: #666;
        font-size: 16px;
      }
      .msg {
        height: 66px;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
      }
      .btnbox {
        height: 50px;
        width: 100%;
        display: flex;
        border-top: 1px solid #e6e6e6;
        .btn1 {
          flex: 1;
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          border-right: 1px solid #e6e6e6;
          color: #999;
        }
        .btn2 {
          flex: 1;
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          color: #4382df;
        }
      }
    }
  }
}
.v-enter,
.v-leave-to {
  opacity: 0;
  transform: translateX(100%);
}
.v-enter-active,
.v-leave-active {
  transition: all 0.5s ease;
}
.btn {
  margin: 8px 0;
  height: 45px;
  width: 60%;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #4382df;
  border-radius: 6px;
  font-size: 20px;
  color: #4382df;
}
.content {
  margin-top: 6px;
  border: 1px solid #e6e6e6;
  width: 94%;
  flex: 1;
  background-color: white;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  margin-bottom: 10px;

  ul {
    width: 100%;
    flex: 1;
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
    li {
      flex-shrink: 0;
      height: 38px;
      width: 100%;
      display: flex;
      .bbox {
        height: 100%;
        width: 66.66666667%;
        display: flex;
        flex-shrink: 0;
      }
      .box {
        white-space: nowrap;
        height: 100%;
        flex: 1;
        flex-shrink: 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align: center;
        font-size: 12px;
        border-right: 1px solid #e6e6e6;
        border-bottom: 1px solid #e6e6e6;
        box-sizing: border-box;
        overflow: hidden;
        overflow-x: scroll;
        padding: 0 2px;
      }
    }
  }
  .top {
    height: 40px;
    width: 100%;
    display: flex;
    border-bottom: 1px solid #e6e6e6;
    .box {
      height: 100%;
      flex: 1;
      font-size: 14px;
      border-right: 1px solid #fff;
      box-sizing: border-box;
      display: flex;
      align-items: center;
      justify-content: center;
      color: #4382df;
    }
  }
}
.titile {
  margin-top: 16px;
  height: 56px;
  width: 94%;
  background-color: white;
  border: 1px solid #4382df;
  border-radius: 6px;
  display: flex;
  .box {
    height: 100%;
    flex: 1;
    .top {
      height: 50%;
      width: 100%;
      color: #4382df;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    .bottom {
      height: 50%;
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
}
.img1 {
  height: 20px;
  width: 20px;
  position: absolute;
  left: 10px;
  top: 15px;
}
.img2 {
  height: 22px;
  width: 22px;
}

.app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100%;
  width: 100%;
  background-color: #f9f9f9;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
  position: fixed;
}

#nav {
  width: 100%;
  height: 50px;
  background-color: #4382df;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  div {
    position: absolute;
    right: 10px;
    border: solid #fff 1px;
    border-radius: 6px;
    color: white;
    font-size: 12px;
    padding: 1.5px 3px;
  }
  p {
    color: white;
    font-size: 20px;
    height: 22px;
    display: block;
    line-height: 22px;
    margin-left: 2px;
  }
}

#nav a {
  font-weight: bold;
  color: #2c3e50;
}

#nav a.router-link-exact-active {
  color: #42b983;
