<template>
<div class="jl">
    <div class="nav">
        <img src="../assets/back.png" alt class="img1" @click="$emit('tap')" />
        <img src="../assets/search.png" alt class="img2" />
        <p>订单统计</p>
        <div @click="sxF = true">筛选</div>
    </div>
    <div class="titile">
        <div class="box">
            <div class="top">总单数</div>
            <div class="bottom">{{zds }}</div>
        </div>
        <div class="box">
            <div class="top">总数量</div>
            <div class="bottom">{{zsl}}</div>
        </div>
    </div>
    <ul class="jlcontent" @scroll="scrollEvent">
        <li class="item" v-for="item in msg" :key="item.key" style="border-radius:4px;overflow:hidden;border:solid 1px #4382df;border-radius:4px;">
            <div style="border-bottom:solid 1px #4382df;border-radius:4px;">
                <div class="line">
                    <div class="lineBox">
                        <p style="color:#4382df">客户：</p>
                        <p>{{item.key}}</p>
                    </div>
                    <div class="lineBox">
                        <p style="color:#4382df">单数：</p>
                        <p>{{item.zds}}</p>
                    </div>
                </div>
                <div class="line">
                    <div class="lineBox">
                        <p style="color:#4382df">匹数：</p>
                        <p>{{item.zps}}</p>
                    </div>
                    <div class="lineBox">
                        <p style="color:#4382df">数量：</p>
                        <p>{{parseFloat(item.zsl)}}</p>
                    </div>
                </div>
            </div>
            <div :style="i === item.value.length-1 ? '' : 'border-bottom:dashed 1px #ccc;'" v-for="(item2,i) in item.value" :key="i" @click="goxq({xh:item2.xh,dh:item2.dh})">
                <div class="line">
                    <div class="lineBox">
                        <p>品名：</p>
                        <p>{{item2.pm}}</p>
                    </div>
                    <div class="lineBox">
                        <p>颜色：</p>
                        <p>{{item2.ysmc}}</p>
                    </div>
                </div>
                <div class="line">
                    <div class="lineBox">
                        <p>单号：</p>
                        <p>{{item2.dh}}</p>
                    </div>
                    <div class="lineBox">
                        <p>数量：</p>
                        <p>{{parseFloat(item2.sl)}}</p>
                    </div>
                </div>
                <div class="line">
                    <div class="lineBox">
                        <p>匹数：</p>
                        <p>{{parseFloat(item2.ps)}}</p>
                    </div>
                    <div class="lineBox">
                        <p>规格：</p>
                        <p>{{item2.gg}}</p>
                    </div>
                </div>
                <div class="line">
                    <div class="lineBox">
                        <p>日期：</p>
                        <p>{{item2.rq.substring(0,11)}}</p>
                    </div>
                    <div class="lineBox">
                        <p>状态：</p>
                        <p>{{item2.zt}}</p>
                    </div>
                </div>
            </div>
        </li>
    </ul>
    <transition>
        <djxq @tap="djTap" v-if="showxq" :option="option"></djxq>
    </transition>
    <div class="sxmask" v-show="sxF" :class="{maskAn:sxF}">
        <div class="sxbox">
            <div class="div">
                <!--eslint-disable-->
                <pre>客　　户：</pre>
                <select v-model="chooseMsg.khmc">
                    <option value selected="selected">请选择客户</option>
                    <option :value="item.khmc" v-for="item in khList" :key="item.khmc">{{item.khmc}}</option>
                </select>
            </div>
            <div class="div">
                <pre>品　　名：</pre>
                <select v-model="chooseMsg.pm">
                    <option value selected="selected">请选择品名</option>
                    <option :value="item.pm" v-for="(item,i) in pmList" :key="i">{{item.pm}}</option>
                </select>
            </div>
            <div class="div">
                <pre>单　　号：</pre>
                <select v-model="chooseMsg.dh">
                    <option value selected="selected">请选择单号</option>
                    <option :value="item.dh" v-for="(item,i) in dhList" :key="i">{{item.dh}}</option>
                </select>
            </div>
            <div class="div">
                <pre>开始日期：</pre>
                <input type="date" v-model="chooseMsg.ksrq" />
            </div>
            <div class="div">
                <pre>结束日期：</pre>
                <input type="date" v-model="chooseMsg.jsrq" />
            </div>
            <div class="btndiv">
                <div class="btnBox" @click="sxF = false">取消</div>
                <div class="btnBox" @click="confirmChoose">确定</div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import djxq from "./djxq.vue";
export default {
    name: "jhjl",
    data() {
        return {
            showxq: false,
            msg: [],
            option: {},
            sxF: false,
            chooseMsg: {
                khmc: '',
                pm: '',
                dh: '',
                ksrq: '',
                jsrq: ''
            },
            khList: [],
            pmList: [],
            dhList: []
        };
    },
    created() {
        this.get();
        this.getKhmc();
        this.getPm();
        this.getDh();
    },
    computed: {
        zds() {
            return this.msg.reduce((total, item) => {
                return (total += item.zds);
            }, 0);
        },
        zsl() {
            return this.msg.reduce((total, item) => {
                return (total += item.zsl);
            }, 0);
        }
    },
    methods: {
        getKhmc() {
            this.$request("scdjd/cxkh").then(res => {
                if (res && res.length > 0 && res instanceof Array) {
                    this.khList = res;
                }
            })
        },
        getPm() {
            this.$request("scdjd/cxpm").then(res => {
                if (res && res.length > 0 && res instanceof Array) {
                    this.pmList = res;
                }
            })
        },
        getDh() {
            this.$request("scdjd/cxscddh").then(res => {
                if (res && res.length > 0 && res instanceof Array) {
                    this.dhList = res;
                }
            })
        },
        confirmChoose() {
            this.get().then(() => {
                this.sxF = false;
                window.console.log(this.chooseMsg)
            })

        },
        goxq(option) {
            this.option = option;
            this.djTap();
        },
        get() {
            return new Promise(resolve => {
                this.$request("scdjd/cxscdjd", this.chooseMsg).then(res => {
                    if (res && res.length > 0 && res instanceof Array) {
                        let obj = {};
                        res.forEach(item => {
                            if (obj[item.khmc]) {
                                obj[item.khmc].push(item);
                            } else {
                                obj[item.khmc] = [item];
                            }
                        });
                        this.msg = [];
                        Object.keys(obj).forEach(item => {
                            let zsl = 0,
                                zps = 0,
                                zds = 0,
                                obj2 = {};
                            obj[item].forEach(VItem => {
                                zsl += parseFloat(VItem.sl);
                                zps += parseFloat(VItem.ps);
                                if (!obj2[VItem.dh]) {
                                    obj2[VItem.dh] = VItem.dh;
                                    zds += 1;
                                }
                            });
                            this.msg.push({
                                key: item,
                                value: obj[item],
                                zsl,
                                zps,
                                zds
                            });
                        });
                        resolve()
                    } else {
                        this.$toast({
                            message: "暂无记录！",
                            duration: 1000
                        });
                        resolve()
                    }
                });
            })
        },
        djTap() {
            this.showxq = !this.showxq;
        },
        tap() {
            this.$emit("tap");
        },
        scrollEvent() {
            var afterS = event.target.scrollTop;
            var a = afterS - this.beforeS;
            if (a > 0) {
                if (
                    event.target.scrollTop + event.target.offsetHeight >=
                    event.target.scrollHeight
                ) {
                    this.$toast({
                        message: "已经到达底部",
                        duration: 1000
                    });
                }
            } else if (a < 0) {
                if (
                    event.target.scrollTop + event.target.offsetHeight <=
                    event.target.offsetHeight
                ) {
                    this.$toast({
                        message: "已经到达顶部",
                        duration: 1000
                    });
                }
            }
            this.beforeS = afterS;
        }
    },
    components: {
        djxq
    }
};
</script>

<style lang="scss" scoped>
.maskAn {
    animation: maskshow 0.5s linear both;
}

.maskAn2 {
    animation: maskshow 0.5s linear both;
}

@keyframes maskshow {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes maskhide {
    from {
        opacity: 1;
    }

    to {
        opacity: 0;
    }
}

.sxmask {
    position: fixed;
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;

    .sxbox {
        width: 80%;
        height: 50%;
        background-color: #fff;
        border-radius: 6px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        .btndiv {
            flex-shrink: 0;
            border-top: solid 1px #e5e5e5;
            width: 100%;
            margin-top: 10px;
            display: flex;
            justify-content: space-around;

            .btnBox:first-child {
                border-right: solid 1px #e5e5e5;
            }

            .btnBox {
                width: 50%;
                height: 50px;
                display: flex;
                justify-content: center;
                align-items: center;
            }
        }

        .div {
            flex-shrink: 0;
            width: 100%;
            margin-top: 18px;
            display: flex;
            justify-content: center;

            pre {
                height: 26px;
                line-height: 26px;
                text-align: center;
            }
        }

        select {
            width: 50%;
            border-radius: 4px;
            height: 26px;
        }

        input {
            width: 50%;
            border-radius: 2px;
            height: 26px;
            box-shadow: none;
        }
    }
}

.jl {
    position: fixed;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    background-color: #f9f9f9;

    .jlcontent {
        width: 100%;
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
        list-style-type: none;
        overflow: hidden;
        overflow-y: scroll;

        .item {
            flex-shrink: 0;
            margin-top: 10px;
            width: 94%;
            background-color: white;
            border-radius: 4px;
            box-shadow: 0 0 5px #e5e5e5;
            display: flex;
            flex-direction: column;

            .line {
                height: 40px;
                width: 100%;
                display: flex;

                .lineBox {
                    flex: 1;
                    height: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: flex-start;
                    padding: 0 0 0 16px;
                    box-sizing: border-box;
                    overflow: hidden;

                    p:nth-child(2) {
                        flex: 1;
                        height: 100%;
                        display: flex;
                        align-items: center;
                        margin-right: 2px;
                        overflow-y: scroll;
                        white-space: nowrap;
                    }
                }
            }
        }

        .item:last-child {
            margin-bottom: 10px;
        }
    }

    .nav {
        width: 100%;
        height: 50px;
        background-color: #4382df;
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;

        div {
            position: absolute;
            right: 10px;
            border: solid #fff 1px;
            border-radius: 6px;
            color: white;
            font-size: 12px;
            padding: 1.5px 3px;
        }

        .img1 {
            height: 20px;
            width: 20px;
            position: absolute;
            left: 10px;
            top: 15px;
        }

        .img2 {
            height: 22px;
            width: 22px;
        }

        p {
            color: white;
            font-size: 20px;
            height: 22px;
            display: block;
            line-height: 22px;
            margin-left: 2px;
        }
    }
}
</style>
