<template>
  <div class="jl">
    <div class="nav">
      <img src="../assets/back.png" alt class="img1" @click="tap" />
      <img src="../assets/search.png" alt class="img2" />
      <p>交货记录</p>
    </div>
    <ul class="jlcontent" @scroll="scrollEvent">
      <li class="item" v-for="(item,i) in msg" :key="i">
        <div class="line">
          <div class="lineBox">
            <p>品名：</p>
            <p>{{item.pm}}</p>
          </div>
          <div class="lineBox">
            <p>工序：</p>
            <p>{{item.gxmc}}</p>
          </div>
        </div>
        <div class="line">
          <div class="lineBox">
            <p>颜色：</p>
            <p>{{item.ysmc}}</p>
          </div>
          <div class="lineBox">
            <p>匹数：</p>
            <p>{{item.ps}}</p>
          </div>
        </div>
        <div class="line">
          <div class="lineBox">
            <p>规格：</p>
            <p>{{item.gg}}</p>
          </div>
          
          <div class="lineBox">
            <p>缸号/机号：</p>
            <p>{{item.gh}}</p>
          </div>
        </div>
        <div class="line">
          <div class="lineBox">
            <p>交货数量：</p>
            <p>{{parseFloat(item.jhsl)}}</p>
          </div>
          <!-- <div class="lineBox">
            <p>状态：</p>
            <p>{{item.jd}}</p>
          </div> -->
        </div>
      </li>
    </ul>
  </div>
</template>

<script type="text/javascript">
export default {
  name: "jhjl",
  data() {
    return {
      msg:[]
    };
  },
  created() {
    this.get()
  },
  methods: {
    get() {
      this.$request("jh/cxscdjhjl").then(res => {
        if(res && res.length > 0 && res instanceof Array){
          this.msg = res;
        }else{
          this.$toast({
            message: "暂无记录！",
            duration: 1000
          });
        }
      });
    },
    tap() {
      this.$emit("tap");
    },
    scrollEvent() {
      var afterS = event.target.scrollTop;
      var a = afterS - this.beforeS;
      if (a > 0) {
        if (
          event.target.scrollTop + event.target.offsetHeight >=
          event.target.scrollHeight
        ) {
          this.$toast({
            message: "已经到达底部",
            duration: 1000
          });
        }
      } else if (a < 0) {
        if (
          event.target.scrollTop + event.target.offsetHeight <=
          event.target.offsetHeight
        ) {
          this.$toast({
            message: "已经到达顶部",
            duration: 1000
          });
        }
      }
      this.beforeS = afterS;
    }
  },
  components: {}
};
</script>

<style lang="scss" scoped>
.jl {
  position: fixed;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  background-color: #f9f9f9;
  .jlcontent {
    width: 100%;
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    list-style-type: none;
    overflow: hidden;
    overflow-y: scroll;
    .item {
      flex-shrink: 0;
      margin-top: 10px;
      width: 94%;
      height: 160px;
      background-color: white;
      border-radius: 4px;
      box-shadow: 0 0 5px #e5e5e5;
      display: flex;
      flex-direction: column;
      .line {
        flex: 1;
        width: 100%;
        display: flex;
        .lineBox {
          flex: 1;
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: flex-start;
          padding: 0 0 0 16px;
          box-sizing: border-box;
          overflow: hidden;
          p:nth-child(2) {
            flex: 1;
            height: 100%;
            display: flex;
            align-items: center;
            margin-right: 2px;
            overflow-y: scroll;
            white-space: nowrap;
          }
        }
      }
    }
    .item:last-child {
      margin-bottom: 10px;
    }
  }
  .nav {
    width: 100%;
    height: 50px;
    background-color: #4382df;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    .img1 {
      height: 20px;
      width: 20px;
      position: absolute;
      left: 10px;
      top: 15px;
    }
    .img2 {
      height: 22px;
      width: 22px;
    }
    p {
      color: white;
      font-size: 20px;
      height: 22px;
      display: block;
      line-height: 22px;
      margin-left: 2px;
    }
  }
}
</style>
